import { Component } from '@angular/core';
import { ProductService } from '../shared/product.service';
import { ToastService } from '../../shared/services/toast.service';
import { IonInfiniteScroll } from '@ionic/angular';
// import { Product } from '../shared/product';
import { CartService } from 'src/app/cart/shared/cart.service';
import { BehaviorSubject } from 'rxjs';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss'],
})
export class ListComponent {
  products = [];
  page = 1;
  $searchTerm = new BehaviorSubject<string>('');

  constructor(
    private productService: ProductService,
    private cartService: CartService,
    private toastService: ToastService
  ) {
    this.productService.search(this.$searchTerm, this.page).subscribe(res => {   
      this.products = res.data;
      this.checkProducts(this.products);
      this.page = 1;
    });
  }

  ionViewWillEnter() {
    // this.loadProducts();
  }

  search(query) {
    this.$searchTerm.next(query);
  }

  addToCart(product) {
    this.cartService.addToCart(product);
    this.toastService.showToast(` ${product.product_th} `, 'top');
  }

  loadData(infiniteScroll: IonInfiniteScroll) {
    this.page++;
    this.productService.search(this.$searchTerm, this.page).subscribe(res => {
      if(Object.keys(res).length === 0 && res.constructor === Object) {
        infiniteScroll.disabled = true;
      }
      setTimeout(() => {
        this.products = this.products.concat(res.data);    
        this.checkProducts(this.products);   
        infiniteScroll.complete();

        if(this.page >= res['last_page']) {
          infiniteScroll.disabled = true;
        }
      }, 500);
    });
  }

  checkProducts(products) {
    products.forEach(product => {
      if(this.cartService.checkInCart(product)) {
        product.incart = 1;
      } else {
        product.incart = 0;
      }

      if(product.instock == 0) {
        product.allowAdd = 0;
      } else {
        product.allowAdd = 1;
      }
    });
  }

}
